.navigation__link {
  letter-spacing: .01em;
  color: var(--main-text-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16;
  text-decoration: none;
}

.navigation__item {
  position: relative;
}

.navigation__item:hover, .navigation__item:focus {
  color: var(--second-title-color);
}

.navigation__select__item:after {
  content: "";
  width: 0;
  height: 0;
  border: 4px solid rgba(0, 0, 0, 0);
  border-bottom-color: #008cef;
  display: block;
  position: absolute;
  top: 7px;
  right: -10px;
}

.services {
  display: inline-block;
  position: relative;
}

.services__button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.services__drop {
  width: 135px;
  background-color: rgba(0, 0, 0, 0);
  display: none;
  position: absolute;
  left: -35%;
}

.services__drop__link {
  letter-spacing: .01em;
  color: var(--main-text-color);
  font-size: 16px;
  line-height: 1.18;
}

.services__drop a {
  text-decoration: none;
  display: block;
}

.navigation__select__item:hover .services__drop {
  display: block;
}

.navigation__select__item {
  transition: opacity 1.5s cubic-bezier(.4, 0, .2, 1);
}

.language {
  position: relative;
}

.language__select {
  cursor: pointer;
}

.language__variations {
  display: none;
  position: absolute;
}

.language__link {
  letter-spacing: .01em;
  color: var(--menu-lang-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
}

.language__variations a {
  text-decoration: none;
  display: inline-block;
}

.language:hover .language__variations {
